import uniStatusBar from "@/components/uni-ui/uni-status-bar/uni-status-bar.vue"
import {mapMutations} from 'vuex';
export default {
	components: {
		uniStatusBar
	}, 
	data() {
		return {
			username:"",
			password:"",
			check:true,
			
			// 验证规则
			rules:{
				username:[
					// {
					// 	rule:/^[a-zA-Z]\w{5,17}$/,
					// 	msg:"账号必须字母开头，长度在6~18之间，只能包含字母、数字和下划线"
					// }
				],
				password:[
					// {
					// 	rule:/^.{5,20}$/,
					// 	msg:"密码长度必须为5-20个字符"
					// }
				]
			},
			
			focusClass:{
				username:false,
				password:false
			},
		}
	},
	methods: {
		...mapMutations(['login']),
		goBack(){
			uni.navigateBack({
				delta: 1
			});
		},
		// 表单验证
		validate(key){
			var check = true
			this.rules[key].forEach((v)=>{
				// 验证失败
				if (!(v.rule).test(this[key])) {
					uni.showToast({ title: v.msg, icon: 'none' });
					check = false
					return false
				}
			})
			return check
		},
		// 提交表单
		submit(){
			if (!this.check) {
				return uni.showToast({
					title: '请先同意XXXXX协议',
					icon: 'none'
				});
			}
			// 验证用户名
			if (!this.validate('username')) return;
			// 验证密码
			if (!this.validate('password')) return;
			
			console.log('提交成功');
			// uni.showLoading({
			// 	title: '登录中...',
			// 	mask: true
			// });
			this.$H.post('/login',{
				username:this.username,
				password:this.password
			}).then(res=>{
				// 状态存储
				this.login(res)
				
				uni.hideLoading()
				uni.showToast({
					title: '登录成功',
					icon: 'none'
				});
				uni.navigateBack({
					delta: 1
				});
			})
		},
		forget(){
			
		},
		focus(key){
			this.focusClass[key] = true
		},
		blur(key){
			this.focusClass[key] = false
		}
	}
}
